@import '../stylesheet/libs/normalize.css';
@import '../stylesheet/fonts.css';

@value variables: '../../stylesheet/vars.css';
@value mainTextColor from variables;

html {
  box-sizing: border-box;
  color: mainTextColor;
  font-family: Inter;
  font-weight: normal;
  font-style: normal;
}

*,
*::before,
*::after {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
}

â€‹img {
  max-width: 100%;
  display: block;
  height: auto;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
}

p {
  margin: 0;
}

ul,
li {
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  text-decoration: none;
}

button:hover {
  cursor: pointer;
}

button:focus {
  outline: none;
}

input:focus,
input:active,
button:active,
button:focus {
  outline: none;
}
